import axios from "axios"
import { useForm } from "react-hook-form"
import { ProvincesOptions } from "../components/ProvincesOptions"
import { useEffect, useState } from "react"
import debounce from 'lodash/debounce';

export const Create = ()=>{
    const onSubmit = async (data, e)=>{
        e.preventDefault()
        try{
            const response = await axios.post(process.env.REACT_APP_API_BASE_URL + '/trips/create',
            {data},
            {withCredentials: true}
            ,)
            if(response.status === 200){
                alert('Trip created successfully!');
                reset()
            }
            else {
                alert('something went wrong')
            }
        }
        catch{
            alert('internal error, try again')
        }
    }
    const {register, handleSubmit, reset} = useForm()
    const [fromProvInput, setFromProvInput] = useState('')
    const [cities, setCities] = useState(null)
    const handleFromInput = (e)=>{
        setFromProvInput(e.target.value);
    }
    useEffect(()=>{
        alert('here')
        if(fromProvInput.trim() == '') return
        const fetchCites = debounce(async()=>{
            let url = `http://api.positionstack.com/v1/forward?access_key=9cec2106fdf303b9e82a25feb084b836&query=${fromProvInput} CA`
            const res = await fetch(url)
            setCities(await res.json(), 2000)
        })
        if(fromProvInput.trim().length> 3)
            fetchCites()
        
        return fetchCites.cancel()
    }, [fromProvInput, setFromProvInput])
    return (
        <form onSubmit={handleSubmit(onSubmit)}>
      <h1>Post Your Trip</h1>
      <input type="address" {...register('departure')} onKeyUp={(e)=>{handleFromInput(e)}} placeholder="departure address" list="fromProvList"/>
      <input {...register('from')} type="text"  placeholder="From" />
      <select {...register('fromProv')} defaultValue="" required>
        <ProvincesOptions />
      </select>
      <datalist id="fromProvList">
        {cities && cities.map((city, index) => (<option key={index}>
            {`${city.name}, ${city.region_code}`}</option>))}
      </datalist>
      <input {...register('destiny')} type="text" placeholder="Destination" />
      <select {...register('destinyProv')} defaultValue="" required>
        <ProvincesOptions />
      </select>
      <input {...register('passagerLimit')} type="number" min={1} max={7} placeholder="Available seats" />
      <input {...register('date')} type="date" placeholder="Date" />
      
      <input type="submit" value="Post" />
    </form>
    )
}